<template>
  <div class="ui-content">
    <div class="content-list-wrap">
      <div class="content-list" v-for="(item, index) in list">
        <p class="item-picture-date">{{item.img.dateType}}</p>
        <p class="issue-no">{{item.issueno}}</p>
        <router-link :to="item.img.destUrl" class="ui-link">
          <img :src="item.img.url" class="item-picture-img">
        </router-link>
        <p class="picture-author">{{item.img.imgType}}&nbsp;|&nbsp;{{item.img.imgAuthor}}</p>
        <p class="picture-content">{{item.des.quote}}</p>
      </div>
      <div class="content-list-loading"></div>
      <div class="content-list-footer"></div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import data from '../../common/json/data.json'
  export default {
    data () {
      return {

      }
    },
    created () {
      this.list = data.express
    }
  }
</script>
<style type="text/css">
  .content-list-wrap{background: #f6f6f6;font-size: 1em;}
  .content-list{margin-top: 3.25rem;background: #f6f6f6;overflow: hidden;}
  .item-picture{padding-bottom: 1.5625rem}
  .content-list .item-text{background: #fff;margin:0 0 0.5em 0;box-shadow: 0 0.0625em 0 0 rgba(230, 230, 230, 0.5)}
  .content-list p{margin:0 1.25rem;}
  .item-picture-date{color: #000;opacity: 0.6;font-size: 1.25rem;line-height: 1.375rem;text-align: center;padding-top: 1rem;font-family: 'Courier New';}
  .content-list .issue-no{margin-top: 0.25rem;margin-bottom: 0;}
  .item-picture-img{width: 100%;margin: 0.625rem 0 0.9375rem;}
  .picture-content{color: 000;opacity: 0.6;font-size: 0.875rem;line-height: 1.625rem;margin: 1.25rem 0 1.125rem;}
</style>



