const SHOW_NAV = 'SHOW_NAV'
const HIDE_NAV = 'HIDE_NAV'
const INITIALIZE_DATA = 'INITIALIZE_DATA'

export default {
  // 显示左侧菜单栏
  [SHOW_NAV] (state) {
    state.isShowNav = true
  },
  // 关闭左侧菜单栏
  [HIDE_NAV] (state) {
    state.isShowNav = false
  },
  // 初始化数据
  [INITIALIZE_DATA] (state) {
    state.isShowNav = false
  }
}



