<template>
  <div class="ui-content index-page">
    <div class="item-issue" v-for="(item, index) in content">
      <div class="issue-pic">
        <div class="separate-item-line"></div>
        <p class="date">{{item.date}}&nbsp;<span class="issue-no">{{item.issueno}}</span></p>
        <a href="/one/1826" class="ui-link">
          <img class="item-pic-img" :src="item.img.url" alt="首页图">
        </a>
        <p class="text-content-short">{{item.des.quote}}</p>
      </div>
      <div class="issue-article">
        <div class="separate-item-line"></div>
        <p class="text-title">
          <router-link to="item.article.url" class="ui-link">
        {{item.article.title}}</router-link><span class="text-author">&nbsp;&nbsp;&nbsp;&nbsp;作者／{{item.article.author}}</span>
        </p>
      </div>
      <div class="issue-question">
        <div class="separate-item-line"></div>
        <p class="text-title"><router-link :to="item.question.url" class="ui-link">
        {{item.question.title}}</router-link></p>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import data from '../../common/json/data.json'
  export default {
    data () {
      return {

      }
    },
    created () {
      this.content = data.express
    }
  }
</script>
<style type="text/css">
  .item-issue{
    margin-top: 3.875rem;
    margin-bottom: 5.5rem;
  }
  .issue-pic,.issue-article, .issue-question, .issue-thing{margin-bottom: 0.625rem;}
  .separate-item-line{height: 1px;background-color: #d4d4d4;margin: 0 1.25rem;}
  .item-issue p{margin:0 1.25rem;}
  .issue-no{color: #808080;font-size: 0.625rem;line-height: 0.875rem;text-align: center;margin-top: 0.25rem;}
  .item-pic-img{width: 100%;margin-top: 10px 0 15px;}
</style>



