<template>
  <header class="header">
    <div class="tab">
      <div class="tab-item">
        <a @click.stop="showNav()" class="ui-link">
          <i class="one-icon one-icon-menu menu"></i>
        </a>
      </div>
      <div class="tab-item center">
        <span>{{label}}</span>
      </div>
      <div class="tab-item">
        <a class="ui-link" id="header-search-btn">
          <i class="one-icon one-icon-search search"></i>
        </a>
      </div>
    </div>
  </header>
</template>
<script type="text/javascript">
  import { mapActions } from 'vuex'
  export default {
    name: 'header',
    data () {
      return {
        label: ''
      }
    },
    methods: {
      ...mapActions(['showNav']),
      chooseLabel () {
        let path = this.$route.path
        if (path === '/' || path === '/index') {
          return '一个'
        } else if (path === '/one') {
          return '一个图文'
        } else if (path === '/article') {
          return '一个阅读'
        } else if (path === '/music') {
          return '一个音乐'
        } else if (path === '/movie') {
          return '一个影视'
        } else if (path === '/apps') {
          return 'App下载'
        } else if (path === '/about') {
          return '关于一个'
        }
      },
      changeLabel () {
        this.label = this.chooseLabel()
      }
    },
    created () {
      this.changeLabel()
    },
    watch: {
      // 如果路由有变化，再次执行该方法
      '$route': 'changeLabel'
    }
  }
</script>
<style>
  .header{
    z-index: 99;
    width: 100%;
    height: 2.75rem;
    background: #fff;
    position: fixed;
    top: 0;
    margin-bottom: 0.5rem;
  }
  .tab{
    height: 100%;
    display: flex;
  }
  .tab .tab-item{
    flex: 1;
    line-height: 44px;
  }
  .tab .center{
    text-align: center;
  }
  .one-icon-menu:before{
    content: "\e65b";
  }
  .one-icon-search:before{
    content: "\e645";
  }
  .search {
    float: right;
    font-size: 1.375rem;
    padding: 0 0.625rem;
  }
</style>



