<template>
  <div>
    <!-- 头部 -->
    <v-header></v-header>
    <!-- 侧边栏导航切换 -->
    <sideNav></sideNav>
    <div class="click" @click.stop = "hideNav()"></div>
    <!-- 主体 -->
    <router-view></router-view>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import header from './components/header/header.vue'
import sideNav from './components/sideNav/sidenav.vue'
export default {
  name: 'app',
  data () {
    return {
      path: '',
      label: ''
    }
  },
  methods: {
    ...mapActions([
      'initializeData', 'hideNav'
    ])
  },
  components: {
    'v-header': header,
    sideNav
  }
}
</script>

<style>
.click{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>



