<template>
  <div>
    <transition name="fade">
      <div v-show="isShowNav">
        <div id="left-panel" class="ui-panel">
          <div class="ui-panel-inner">
            <ul class="ui-listview">
              <li v-for="(item, index) in navItems" @click ="hideNav()" >
                <router-link :to="item.url">{{item.name}}</router-link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script type="text/javascript">
  import data from '../../common/json/data.json'
  import { mapState, mapActions } from 'vuex'
  export default {
    name: 'sidenav',
    props: {

    },
    data () {
      return {
        navItems: []
      }
    },
    methods: {
      ...mapActions([
        'initializeData', 'hideNav'
      ])
    },
    created () {
      this.navItems = data.navItems
      this.initializeData()
    },
    computed: mapState([
      'isShowNav' // 是否显示左侧菜单
    ])
  }
</script>
<style type="text/css">
  .ui-panel{
    display: block;
    width: 11.75rem;
    min-height: 100%;
    max-height: none;
    border-width: 0;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    border-color: #1d1d1d;
    /*color: #fff;
    text-shadow: 0 0.0625rem 0 #111;*/
    box-shadow: 0.3125rem 0 0.3125rem rgba(0, 0, 0, .15);
    z-index: 1001;
  }
  .fade-enter-active, .fade-leave-active{
    transition: transform 300ms ease;
  }
  .fade-enter{
    transform: translate3d(-17em,0,0);
  }
  .fade-leave-to{
    transition: transform 300ms ease;
    transform: translate3d(-17em,0,0);
  }
  .ui-panel-inner {
    padding: 1em;
    margin-top: 1.875rem
  }
  .ui-listview{
    margin: -1em;
  }
  .ui-listview li{
    display: block;
    position: relative;
    overflow: visible;
    text-align: center;
  }
  .ui-listview a{
    display: block;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: rgba(0,0,0,0.6);
    text-shadow: none;
    font-weight: lighter;
    font-size: 1rem;
    padding: .7em 1em;
    user-select: none;
    height: 1.875rem;
    line-height: 1.875rem;
    vertical-align: middle;
    text-align: center;
  }
  .ui-listview a:link{
    background: transparent;
  }
</style>



