<template>
  <div class="ui-content">
    <div class="home-img" :style = "{'backgroundImage':'url('+ imgObj.url + ')','height':imgObj.height,'width':imgObj.width}"></div>
    <!-- 遮罩 -->
    <div class="sentence-bg"></div>
    <!-- 图片文字说明 -->
    <div class="sentence">
      <div class="s-container">
        <a href="/one" class="ui-link div-link">
          <p class="day">{{des.day}}</p>
          <p class="month">{{des.month}}&nbsp;</p>
          <p class="text-content-short">{{des.quote}}</p>
        </a>
        <div class="bottom-center">
          <a href="#home-text-anchor" class="ui-link anchor-link">
            <img src="../../../static/image/m.wufazhuce.com-arrow-down.png">
          </a>
        </div>
      </div>
    </div>
    <!-- 锚点 -->
    <div class="home-text-anchor" id="home-text-anchor"></div>
    <div class="home-text">
      <div class="article">
        <p class="text-type"><router-link :to="article.url1" class="ui-link">{{article.type}}｜</router-link></p>
        <div class="link-div">
          <p class="text-title">{{article.title}}</p>
          <p class="text-author" v-if="article.author">作者／{{article.author}}</p>
          <p class="text-content-short">{{article.passage[0].part}}</p>
          <p class="text-more"><router-link :to="article.url" class="ui-link">阅读全文</router-link></p>
        </div>
      </div>
      <div class="question">
        <p class="text-type"><router-link :to="question.url1" class="ui-link">{{question.type}}｜</router-link></p>
        <div class="link-div">
          <p class="text-title">{{question.title}}</p>
          <p class="text-author" v-if="question.author">作者／{{question.author}}</p>
          <p class="text-content-short">{{question.passage[0].part}}</p>
          <p class="text-more"><router-link :to="question.url" class="ui-link">阅读全文</router-link></p>
        </div>
      </div>
    </div>
    <router-link to="/index" class="ui-btn more-btn">更多内容</router-link>
  </div>
</template>
<script type="text/javascript">
import data from '../../common/json/data.json'
export default {
  data () {
    return {
      bgImg: {},
      content: []
    }
  },
  created () {
    let currentMsg = data.express[0]
    this.imgObj = currentMsg.img
    this.des = currentMsg.des
    this.article = currentMsg.article
    this.question = currentMsg.question
  }
}
</script>
<style type="text/css">
  .home-img{
    position: absolute;
    background: no-repeat center center;
    -webkit-background-size: cover;
    background-size: cover;
    z-index: -2;
  }
  .sentence-bg{
    height: 41.75rem;
    background: rgba(0, 0, 0, 0.2);
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.8));
    z-index: -1;
  }
  .sentence{height: 41.75rem;width: 100%;position: absolute;top: 0;margin-bottom: 1.25rem;}
  .s-container{width: 100%;height: 8.875rem;position: absolute;bottom: 0;left: 0;}
  .link-div{vertical-align: bottom;}
  .div-link{display: block;}
  .sentence p, .home-text p{margin: 0 1.25rem;}
  .div-link p{color: #fff;text-align: left;}
  .div-link .day{font-size: 3.0rem;line-height: 3.0rem;font-family: Serif;}
  .div-link .month{font-size: 1.1875rem;line-height: 1.1875rem;}

  .bottom-center{width: 100%;z-index: 9;height: 1.5625rem;text-align: center;}
  .bottom-center img{width: 1.4375rem;height: 0.75rem;padding: 0.375rem 1.875rem;}
  .home-text{width: 100%;margin-top: 1.25rem;}
  .home-text > div {margin-bottom: 4.375rem;}
  .text-type{color: #29a7e2;font-size: 0.8125rem;}
  .text-more{color: #29a7e2;}
  .more-btn{width: 6.25rem;margin: 0 auto;margin-bottom: 2.5rem;background-color: #f6f6f6;color:#333;text-shadow: 0 0.0625rem 0 #f3f3f3;border-radius: 1em;border:0.0625rem solid #ddd;line-height: 1.3;font-family: sans-serif;}

</style>



