module.exports = {"navItems":[{"name":"图文","url":"/one"},{"name":"阅读","url":"/article"},{"name":"音乐","url":"/music"},{"name":"影视","url":"/movie"},{"name":"App下载","url":"/apps"},{"name":"关于","url":"/about"}],"express":[{"date":"Aug 7, 2017","issueno":"VOL.1797","img":{"url":"../../../static/image/homepage/FgEdwne04LiRGBPehx5CUYDBEgdu.jpg","height":"41.75rem","width":"23.4375rem","imgType":"插画","imgAuthor":"郁唯为","dateType":"2017 / 08 / 07","destUrl":"/one/1826"},"des":{"day":7,"month":"VOL.1797 | Aug. 2017","quote":"愿爱如霞。渐晚，渐浓。","author":"七堇年"},"article":{"date":"2017-8-7","type":"阅 读","type1":"article","title":"和猫在一起","author":"苏枕书","passage":[{"part":"其实大部分时间，我并没有跟猫在一起。我的猫住在北京家里，我却一年超过八个月都住在京都。他们说我是那种不负责任的家长，只知道小孩子可爱，自己却不带小孩子。不过多年以前，收养第一只猫的时候，我的确以为自己是要定居北京，好好过生活的。"},{"part":"那时候的男朋友是北京人，比我大不少，计划是我读完书就结婚。他很忙，我们共处时间不多。敏感多情的少女想养一只猫，他允许了。在豆瓣流浪猫小组，刚好看到有人说，芍药居某小区有只八个月大的流浪猫，白色。我跟发帖人联系，说想收养它。对方考察了我的条件，说可以。我便从南三环坐了很久的车，来到了北边的芍药居。联络人是位温柔的姑娘，正在准备出国，本来自己要收养这只猫。天已见萧索，十月末北京有风的下午。她领我到猫出没的地带，那里有个小纸盒，里面有水与猫粮，是小区的好心人布置。她唤了两声，果然闪过一团白雪，是只非常漂亮的小白猫，盘踞高处，宝石般晶莹深邃的眼眸冷漠地望着我们。“阿咪，阿咪，有人要养你啦。”姑娘拿一根火腿肠引诱它，它迟疑片刻，忽地腾空一跃，叼走了那段火腿肠。姑娘长期做流浪猫救助工作，手法也老道，趁势揪住它脖颈后一片皮毛，顺顺当当抱到怀里，让我装进书包，“好了，它就交给你啦。”"},{"part":"意外地，那团柔软的肉体十分顺从，我抱着它，打车回到南城暂居的小屋。多年以后，几乎不相信自己曾在那里居住过。早晚经过陶然亭公园，听到胡琴与吊嗓声。黄昏，波光粼粼的护城河，杨树涂满夕光的叶片如金箔银箔，相击作声。小区门前昏暗的菜市场，房顶上长满杂草的门钉肉饼小铺子，凭空高高一簇瘦削的菊芋，人们似乎只把它当成观花植物，缩小版的向日葵，并不知道它的块茎可以做酱菜。"},{"part":"我给猫起了很多名字，但都不如“白小姐”这个叫得顺口。磨合期很短暂，很快它就跟我一起作息，同吃同睡。彻夜无所事事看闲书时，它也不睡，趴在我膝头，嗲得我不知所措。午睡时，它也过来，枕我一截手腕，温柔一小团，潮湿的粉色的小鼻子，四五月晴天白云一样轻软悠长的呼吸，我大气不敢出，想是第一次知道被依赖的感觉。"}],"url":"/article/2778","url1":"/article"},"question":{"date":"2017-8-7","type":"问答","type1":"question","title":"年轻人为什么会选择自杀式养生？","passage":[{"part":"今年体检，我的各项指标依然正常，但相比去年，健康状况已经有所下降，本着一颗珍爱生命的心，走出体检大厅时我就暗示自己今年要多运动，争当我圈第一个练出马甲线的妇女，戒奶茶，戒碳酸饮料，每周一吃素，早日过上中产阶级的健康轻生活。"},{"part":"但是今年夏天多热啊，光是听到别人嗒一声打开易拉罐，我就浑身难受，有一天真的憋不住内心对二氧化碳的渴望了，必须要嗑一罐加冰可乐然后打三个大嗝，否则没法继续热爱工作，于是我就开了一罐，爽到飞起。"},{"part":"后来一想不对啊，我上次头晕，去开了些调理气血的药，今天份还没吃呢，既然今天都喝这么不健康的碳酸饮料，药更不能不吃，于是顺手拿起手边的可乐把药咽下去了，动作流畅完美。"}],"url":"/question/1847","url1":"/question"}},{"date":"Aug 1, 2017","issueno":"VOL.1796","img":{"url":"../../../static/image/homepage/homepage.jpg","height":"41.75rem","width":"23.4375rem","imgType":"绘画","imgAuthor":"ames McNeill Whistler","dateType":"2017 / 08 / 01","destUrl":"/one/1826"},"des":{"day":1,"month":"VOL.1796 | Aug. 2017","quote":"时常会怀疑，自己现在做的事有意义吗？选择的道路正确吗？然而这样的追问并不能够得到回应，因为很多事情之所以没有答案，只因为自己正处于。再等等吧，再等等，试着把镜头拉远一点，试着通过一个更长的时间隧道来观望自己，大概就看到更清晰的东西。 。","author":"花大钱"},"article":{"date":"2017-8-1","type":"阅 读","type1":"article","title":"午餐后航行","author":"宋阿曼","passage":[{"part":"那些陡峭的山在寒冷干燥的空气里"},{"part":"也像我们这样，平静而不痛苦吗？[1]"},{"part":"何溪在二十八岁的年龄完全成熟了。在那年漫长的春夏交接中，她得出这个判断，一个由非此不可到别样亦可的过程宣告完成。她觉得自己身体闭合了，发育骤止，不会再朝任何方向生长。她在镜前盯着自己的身体，这个躯体似乎足够余下的时间去消耗。智力、骨骼、脂肪、皮肤的弹性，衰老的痕迹是明显的。她一只手握住自己的乳房，越来越用力，好像这是身体毫无感觉的息肉。她触碰每寸皮肤都是一种陈旧感，碎发疲惫地搭在颧骨，她厌恶镜中那张极度平静而又被欲望催老的脸。只有镜前的灯开着，幽暗的空间配合窗外车流的涌动声，是一种什么东西在缓慢下落的氛围。"},{"part":"我给猫起了很多名字，但都不如“白小姐”这个叫得顺口。磨合期很短暂，很快它就跟我一起作息，同吃同睡。彻夜无所事事看闲书时，它也不睡，趴在我膝头，嗲得我不知所措。午睡时，它也过来，枕我一截手腕，温柔一小团，潮湿的粉色的小鼻子，四五月晴天白云一样轻软悠长的呼吸，我大气不敢出，想是第一次知道被依赖的感觉。"}],"url":"/article/2775","url1":"/article"},"question":{"date":"2017-8-1","type":"问答","type1":"question","title":"如何获得安全感","passage":[{"part":"如何获得安全感？"},{"part":"我最概要的回答是：自讨苦吃。"},{"part":"后来一想不对啊，我上次头晕，去开了些调理气血的药，今天份还没吃呢，既然今天都喝这么不健康的碳酸饮料，药更不能不吃，于是顺手拿起手边的可乐把药咽下去了，动作流畅完美。"}],"url":"/question/1884","url1":"/question"}},{"date":"Jul 29, 2017","issueno":"VOL.1796","img":{"url":"../../../static/image/homepage/FkKj9g840EF2WTwHIFmXeodnb7N8.jpg","height":"41.75rem","width":"23.4375rem","imgType":"摄影","imgAuthor":"Igor Rand","dateType":"2017 / 07 / 29","destUrl":"/one/1825"},"des":{"day":29,"month":"VOL.1793 | Jul. 2017","quote":"命里有时终须有，命里无时需强求 。","author":"三三"},"article":{"date":"2017-7-29","type":"阅 读","type1":"article","title":"我所理解的生活","author":"韩寒","passage":[{"part":"我所理解的生活，做自己想做的事，和自己喜欢的一切在一起"},{"part":"也像我们这样，平静而不痛苦吗？[1]"},{"part":"何溪在二十八岁的年龄完全成熟了。在那年漫长的春夏交接中，她得出这个判断，一个由非此不可到别样亦可的过程宣告完成。她觉得自己身体闭合了，发育骤止，不会再朝任何方向生长。她在镜前盯着自己的身体，这个躯体似乎足够余下的时间去消耗。智力、骨骼、脂肪、皮肤的弹性，衰老的痕迹是明显的。她一只手握住自己的乳房，越来越用力，好像这是身体毫无感觉的息肉。她触碰每寸皮肤都是一种陈旧感，碎发疲惫地搭在颧骨，她厌恶镜中那张极度平静而又被欲望催老的脸。只有镜前的灯开着，幽暗的空间配合窗外车流的涌动声，是一种什么东西在缓慢下落的氛围。"},{"part":"我给猫起了很多名字，但都不如“白小姐”这个叫得顺口。磨合期很短暂，很快它就跟我一起作息，同吃同睡。彻夜无所事事看闲书时，它也不睡，趴在我膝头，嗲得我不知所措。午睡时，它也过来，枕我一截手腕，温柔一小团，潮湿的粉色的小鼻子，四五月晴天白云一样轻软悠长的呼吸，我大气不敢出，想是第一次知道被依赖的感觉。"}],"url":"/article/2730","url1":"/article"},"question":{"date":"2017-7-29","type":"问答","type1":"question","title":"怎样让自己变得快乐","passage":[{"part":"在当今社会，物质欲望越来越多，各种成功迫在眉睫，追名逐利"},{"part":"我最概要的回答是：自讨苦吃。"},{"part":"后来一想不对啊，我上次头晕，去开了些调理气血的药，今天份还没吃呢，既然今天都喝这么不健康的碳酸饮料，药更不能不吃，于是顺手拿起手边的可乐把药咽下去了，动作流畅完美。"}],"url":"/question/1881","url1":"/question"}},{"date":"Jul 24, 2017","issueno":"VOL.1796","img":{"url":"../../../static/image/homepage/Fop7r5O5t8HwzewNlBob6sRaXp0x.jpg","height":"41.75rem","width":"23.4375rem","imgType":"插画","imgAuthor":"Shelia Liu","dateType":"2017 / 07 / 24","destUrl":"/one/1824"},"des":{"day":24,"month":"VOL.1793 | Jul. 2017","quote":"下雨这回事情就像人生中的大多数灾难一样，它若真要发生，不必等到天先阴下来的。 。","author":"阿尔弗雷德·阿德勒"},"article":{"date":"2017-7-24","type":"阅 读","type1":"article","title":"发生问题时别逃走 ","author":"陈雪","passage":[{"part":"我与现在的恋人A在一起这些年，无论我们如何争吵、冷战、发生任何问题，无论我感到怎么挫败，或者我使他如何生气、难受，我们顶多只是各自出去走走，过几个小时，又会回到家里，各自待在房间或客厅，有时他会睡在客房里一夜，曾经有一两次我到朋友家或老师家讨教，但当天我们还是会回家，我们不曾有人离家出走，或者跑去哪儿另找安慰，即使我们无法立刻和好，即使待在安静的屋子里，静默的气氛令人感到难受，我们终究还是回到家，我们会用各自方式让对方知道，“我还在努力”。"},{"part":"也像我们这样，平静而不痛苦吗？[1]"},{"part":"何溪在二十八岁的年龄完全成熟了。在那年漫长的春夏交接中，她得出这个判断，一个由非此不可到别样亦可的过程宣告完成。她觉得自己身体闭合了，发育骤止，不会再朝任何方向生长。她在镜前盯着自己的身体，这个躯体似乎足够余下的时间去消耗。智力、骨骼、脂肪、皮肤的弹性，衰老的痕迹是明显的。她一只手握住自己的乳房，越来越用力，好像这是身体毫无感觉的息肉。她触碰每寸皮肤都是一种陈旧感，碎发疲惫地搭在颧骨，她厌恶镜中那张极度平静而又被欲望催老的脸。只有镜前的灯开着，幽暗的空间配合窗外车流的涌动声，是一种什么东西在缓慢下落的氛围。"},{"part":"我给猫起了很多名字，但都不如“白小姐”这个叫得顺口。磨合期很短暂，很快它就跟我一起作息，同吃同睡。彻夜无所事事看闲书时，它也不睡，趴在我膝头，嗲得我不知所措。午睡时，它也过来，枕我一截手腕，温柔一小团，潮湿的粉色的小鼻子，四五月晴天白云一样轻软悠长的呼吸，我大气不敢出，想是第一次知道被依赖的感觉。"}],"url":"/article/2738","url1":"/article"},"question":{"date":"2017-8-29","type":"问答","type1":"question","title":"经常觉得自己被生活卡住怎么办？ ","passage":[{"part":"越长大越明白那段话：“——是不是人生总是如此艰难，还是只有童年如此？——总是如此。”被生活卡住怎么办？"},{"part":"我最概要的回答是：自讨苦吃。"},{"part":"后来一想不对啊，我上次头晕，去开了些调理气血的药，今天份还没吃呢，既然今天都喝这么不健康的碳酸饮料，药更不能不吃，于是顺手拿起手边的可乐把药咽下去了，动作流畅完美。"}],"url":"/question/1839","url1":"/question"}},{"date":"Jul 20, 2017","issueno":"VOL.1790","img":{"url":"../../../static/image/homepage/FrDZZC-3o3SZowxiNhaP1wc0N1A1.jpg","height":"41.75rem","width":"23.4375rem","imgType":"摄影","imgAuthor":"ominik Schröder","dateType":"2017 / 07 / 20","destUrl":"/one/1823"},"des":{"day":20,"month":"VOL.1790 | Jul. 2017","quote":"其实，人生非常单纯......生活的不确定性正是一个人希望的来源。只想逃避困难的人，必会落后于他人。 。","author":"曹畅洲"},"article":{"date":"2017-7-20","type":"阅 读","type1":"article","title":"没有意义的旅行 ","author":"苏更生","passage":[{"part":"你好呀，秋天的太阳和薄被是最让人温暖的东西。走在街上，睡在床上，因为季节变化而感知的幸福，在此刻最清晰。秋天了，深夜外出的时候必须穿上外套。最近我做了一场短途旅行，在深夜的时候开车出城，独自向北而去。车窗外除了连绵的路灯，黑暗里空无一物。离开城市越远，越觉得越亮堂，月亮的光辉明亮了许多。"},{"part":"也像我们这样，平静而不痛苦吗？[1]"},{"part":"何溪在二十八岁的年龄完全成熟了。在那年漫长的春夏交接中，她得出这个判断，一个由非此不可到别样亦可的过程宣告完成。她觉得自己身体闭合了，发育骤止，不会再朝任何方向生长。她在镜前盯着自己的身体，这个躯体似乎足够余下的时间去消耗。智力、骨骼、脂肪、皮肤的弹性，衰老的痕迹是明显的。她一只手握住自己的乳房，越来越用力，好像这是身体毫无感觉的息肉。她触碰每寸皮肤都是一种陈旧感，碎发疲惫地搭在颧骨，她厌恶镜中那张极度平静而又被欲望催老的脸。只有镜前的灯开着，幽暗的空间配合窗外车流的涌动声，是一种什么东西在缓慢下落的氛围。"},{"part":"我给猫起了很多名字，但都不如“白小姐”这个叫得顺口。磨合期很短暂，很快它就跟我一起作息，同吃同睡。彻夜无所事事看闲书时，它也不睡，趴在我膝头，嗲得我不知所措。午睡时，它也过来，枕我一截手腕，温柔一小团，潮湿的粉色的小鼻子，四五月晴天白云一样轻软悠长的呼吸，我大气不敢出，想是第一次知道被依赖的感觉。"}],"url":"/article/2777","url1":"/article"},"question":{"date":"2017-7-27","type":"问答","type1":"question","title":"渣男体质应该如何自救？  ","passage":[{"part":"我有一个朋友，谈恋爱碰到的都是渣男，每个都浪得不行，最后都要劈腿出轨，为什么上天对她这么残忍，渣男体质应该怎么治？"},{"part":"我最概要的回答是：自讨苦吃。"},{"part":"后来一想不对啊，我上次头晕，去开了些调理气血的药，今天份还没吃呢，既然今天都喝这么不健康的碳酸饮料，药更不能不吃，于是顺手拿起手边的可乐把药咽下去了，动作流畅完美。"}],"url":"/question/1843","url1":"/question"}}]}

